<script>
  import {
    studentData,
    behaviorCategories,
    positiveList,
    informationList,
    level1List,
    YCList,
    OCList,
    RCList,
  } from "../../server/db.js";
  import { onMount } from "svelte";
  import { slide } from "svelte/transition";

  import { fade, fly } from "svelte/transition";
  onMount(() => getHomerooms());
  let response = {
    level: "",
    details: "",
    otherDetails: "",
    behaviorList: [],
  };
  let searchTerm = "";
  // will show up in the multiselect box to choose students to add to the form
  let filteredStudents = [];
  // selected in the dropdown box
  let selectedHomeroom;
  let homerooms = [];
  const getHomerooms = () => {
    // search throug all student data and filter out the homerooms that haven't been added to the list and add them.
    for (let studentObj of studentData) {
      if (!homerooms.includes(studentObj.homeroom)) {
        homerooms = [...homerooms, studentObj.homeroom];
      }
    }
    homerooms = homerooms.sort();
  };
  let behaviorKinds = [];
  function displayBehaviorKinds() {
    if (response.level === "Information") {
      behaviorKinds = informationList;
    } else if (response.level === "Level 1") {
      behaviorKinds = level1List;
    } else if (response.level === "Yellow Level") {
      behaviorKinds = YCList;
    } else if (response.level === "Orange Level") {
      behaviorKinds = OCList;
    } else if (response.level === "Red Level") {
      behaviorKinds = RCList;
    } else behaviorKinds = positiveList;
    uncheckBehaviorList();
  }
  function uncheckBehaviorList() {
    let checkboxes = document.getElementsByName("behaviorList");
    for (let i = 0, n = checkboxes.length; i < n; i++) {
      checkboxes[i].checked = false;
    }
  }
  let otherCheckboxSelected = false;
  let selectedStudents = [];
  let studentsToAddToList = [];
  let studentsToRemoveFromList = [];
  function addStudentToList() {
    // selectedStudents = selectedStudents.concat(studentsToAddToList);
    // loop through all candidates on the add to list.
    for (let i = 0; i < studentsToAddToList.length; i++) {
      let found = false;
      // loop through all curretnly selected students
      if (selectedStudents.some((student) => student.id === studentsToAddToList[i].id)) {
        /* selected Students contains the element we're looking for */
        found = true;
      }
      // if not found, add to the selected Student list
      if (!found) {
        selectedStudents.push(studentsToAddToList[i]);
      }
    }
    selectedStudents.sort((a, b) => (a.name > b.name ? 1 : -1));
    selectedStudents = selectedStudents;
  }
  function removeStudentFromList() {
    for (let i = 0; i < studentsToRemoveFromList.length; i++) {
      for (let j = selectedStudents.length - 1; j >= 0; j--) {
        if (studentsToRemoveFromList[i].id == selectedStudents[j].id) {
          selectedStudents.splice(j, 1);
          selectedStudents = selectedStudents;
        }
      }
    }
  }
  $: if (selectedHomeroom) getStudentsByHr();
  let getStudentsByHr = () => {
    searchTerm = "";
    filteredStudents = "";
    if (selectedHomeroom === "All Homerooms") {
      filteredStudents = studentData.filter((student) => student.homeroom);
    } else {
      filteredStudents = studentData.filter((student) => student.homeroom === selectedHomeroom);
    }
    return filteredStudents.sort((a, b) => (a.name > b.name ? 1 : -1));
  };
  $: if (searchTerm) selectedHomeroom = "";
  let searchStudents = () => {
    if (!(searchTerm.length > 0)) {
      filteredStudents = [];
      filteredStudents = filteredStudents;
      return filteredStudents;
    }
    filteredStudents = studentData.filter((student) => {
      let studentName = student.name.toLowerCase();
      return studentName.includes(searchTerm.toLowerCase());
    });
    return filteredStudents.sort((a, b) => (a.name > b.name ? 1 : -1));
  };
  function resetForm() {
    uncheckBehaviorList();
    response.level = "";
    response.details = "";
    response.otherDetails = "";
    response.behaviorList = [];
    behaviorKinds = [];
    behaviorKinds = behaviorKinds;
    selectedHomeroom = "";
    searchTerm = "";
    otherCheckboxSelected = false;
    selectedStudents = [];
    selectedStudents = selectedStudents;
    filteredStudents = [];
    filteredStudents = filteredStudents;
    studentsToAddToList = [];
    studentsToAddToList = studentsToAddToList;
  }

  function handleSubmit() {
    if (selectedStudents.length === 0) {
      alert("Please select students to submit");
      return;
    }
    if (otherCheckboxSelected) {
      response.behaviorList.push(response.otherDetails);
    }
    console.table(response);
    resetForm();
  }
</script>

<div class="m-2">
  <p>This form contains mock data</p>
  <h1 class="text-2xl text-blue-800 mx-2">Student Merit Form</h1>
  <form on:submit|preventDefault={handleSubmit}>
    <p class="text-blue-800 text-m px-4 italic">
      Start by searching for the student below. Then select the students in Available Students
      section.
    </p>
    <div class="flex justify-center flex-wrap">
      <!-- Search Section Begins -->
      <div class="p-4 m-2 w-96 shadow-lg rounded-lg">
        <legend class="text-xl text-blue-800">Search by Student</legend>
        <input
          class="mt-1 w-full mr-6 rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
          type="text"
          id="search-field"
          placeholder="Enter Student Name"
          autocomplete="off"
          bind:value={searchTerm}
          on:input={searchStudents}
        />
      </div>
      <div class="p-4 m-2 shadow-lg w-96 rounded-lg">
        <legend class="text-xl  text-blue-800">Search by Homeroom</legend>
        <select
          class="mt-1 w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
          name="homeroom"
          id="homeroom"
          bind:value={selectedHomeroom}
        >
          <option disabled selected value="">Select a homeroom.</option>
          <option value="All Homerooms">All homerooms</option>
          {#each homerooms as homeroom}
            <option value={homeroom}>{homeroom}</option>
          {/each}
        </select>
      </div>
    </div>
    <p class="text-blue-800 text-m px-4 italic">
      Next, select the student(s) in the list student below. Tap if on mobile.
    </p>
    <div class="flex justify-center flex-wrap">
      <div class="p-4 m-2 shadow-lg w-96 rounded-lg">
        <div class="flex justify-between">
          <legend class="text-xl text-blue-800">Add Students</legend>
          <button
            on:click={addStudentToList}
            type="button"
            class="inline-block mb-2 px-4 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
            >Add Student</button
          >
        </div>
        {#if filteredStudents.length > 0}
          <p in:fade={{ duration: 1000 }} class="text-red-500 text-xs italic">
            Please select a student
          </p>
        {:else}
          <p in:fade={{ duration: 1000 }} class="text-red-500 text-xs italic">
            Please search for students
          </p>
        {/if}
        <select
          class="mt-1 rounded-md h-32 w-full border-1 border-blue-600 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
          name="students"
          id="students"
          bind:value={studentsToAddToList}
          multiple
        >
          {#each filteredStudents as student}
            <option in:fade={{ delay: 500, duration: 1000 }} value={student}>{student.name}</option>
          {/each}
        </select>
      </div>

      <!-- Selecting students to remove from the form list start -->
      <div class="p-4 m-2 w-96 shadow-lg rounded-lg">
        <div class="flex justify-between">
          <legend class="text-xl text-blue-800">Remove Students</legend>
          <button
            on:click={removeStudentFromList}
            type="button"
            class="inline-block mb-2 px-4 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
            >Remove Student</button
          >
        </div>
        <p class="text-red-500 text-xs italic">
          {#if selectedStudents.length > 0}
            If on mobile tap to view list.
          {:else}
            First add some students.
          {/if}
        </p>
        <select
          class="mt-1 rounded-md h-32 w-full border-1 border-blue-600 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
          name="addedStudents"
          id="addedStudents"
          bind:value={studentsToRemoveFromList}
          multiple
        >
          {#each selectedStudents as student}
            <option
              in:fade={{ delay: 500, duration: 1000 }}
              out:fade={{ duration: 1000 }}
              value={student}>{student.id}: {student.name}</option
            >
          {/each}
        </select>
      </div>
    </div>
    <!-- Selected Student List Component -->
    <div class="flex flex-wrap justify-center">
      <div class="p-4 m-2 shadow-lg w-full rounded-lg">
        <p class="text-xl mb-4  text-blue-800 text-center">Currently Selected</p>
        <p class="text-red-500 text-xs italic">
          {#if selectedStudents.length > 0}
            Your currently selected students.
          {:else}
            First add some students.
          {/if}
        </p>
        <div
          class="flex flex-wrap mt-1 w-full border-1 rounded border-blue-600"
          class:border={selectedStudents.length > 0}
        >
          {#each selectedStudents as student}
            <p
              class="m-1 px-2 py-1 font-semibold text-xs border border-1 bg-blue-600 text-white rounded-full shadow-sm"
              in:fade
              out:fly={{ x: 100 }}
              value={student}
            >
              {student.id}: {student.name}
            </p>
          {/each}
        </div>
      </div>
    </div>
    <p class="text-blue-800 text-m px-4 italic">
      Next, select the Merit Category and then the Merit Type.
    </p>
    <div class="flex justify-center flex-wrap">
      <div class="p-4 m-2 w-96 shadow-lg rounded-lg">
        <legend class="text-xl text-blue-800">Merit Categories</legend>
        {#if !response.level}
          <p in:fade={{ duration: 1000 }} class="text-red-500 text-xs italic">
            Select a Merit category from the list.
          </p>
        {:else}
          <p in:fade={{ duration: 1000 }} class="text-red-500 text-xs italic">
            Now select they information types.
          </p>
        {/if}
        <fieldset>
          {#each behaviorCategories as category}
            <p class="mb-2">
              <input
                type="radio"
                in:fade={{ duration: 500 }}
                name={behaviorCategories}
                id={category}
                value={category}
                bind:group={response.level}
                on:change={displayBehaviorKinds}
              />
              <label for={category} class="ml-2">{category}</label>
            </p>
          {/each}
        </fieldset>
      </div>
      <div class="p-4 m-2 w-96 shadow-lg">
        <legend class="text-xl text-blue-800">Merit Information</legend>
        <fieldset>
          {#if behaviorKinds.length < 1}
            <p class="text-red-500 text-xs italic">Please select a Merit category from the list.</p>
          {/if}
          {#if behaviorKinds.length > 0}
            {#key behaviorKinds}
              <ul class="p-2 mx-auto">
                {#each behaviorKinds as kind}
                  <li
                    class="pt-3"
                    in:slide={{ delay: 500, duration: 1000 }}
                    out:slide={{ duration: 500 }}
                  >
                    <input
                      type="checkbox"
                      class="text-blue-500 border-2 rounded border-blue-500 focus:ring-blue-500"
                      id={kind}
                      bind:group={response.behaviorList}
                      name="behaviorList"
                      value={kind}
                    />
                    <label for={kind} class="ml-2">{kind}</label>
                  </li>
                {/each}
                <li
                  class="pt-2 pb-1"
                  in:slide={{ delay: 500, duration: 1000 }}
                  out:slide={{ duration: 500 }}
                >
                  <div class="flex justify items-center">
                    <input
                      type="checkbox"
                      class="text-blue-500 border-2 mt-2 rounded border-blue-500 focus:ring-blue-500"
                      id="checkboxOther"
                      bind:group={response.behaviorList}
                      bind:checked={otherCheckboxSelected}
                      name="behaviorList"
                      value="other"
                    />
                    <label for="otherValue" class="ml-2 mt-1 mr-2">other</label>

                    {#if otherCheckboxSelected}
                      <input
                        class="w-full rounded-md h-8 border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                        type="text"
                        id="otherValue"
                        name="otherValue"
                        bind:value={response.otherDetails}
                        required
                      />
                    {/if}
                  </div>
                </li>
              </ul>
            {/key}
          {/if}
        </fieldset>
      </div>
    </div>
    <p class="text-blue-800 text-m px-4 italic">
      Finally, write a description of the incident and submit.
    </p>
    <div class="flex justify-center flex-wrap">
      <div class="p-4 m-2 w-96 shadow-lg">
        <label for="details" class="block text-xl text-blue-800">Details</label>
        <textarea
          class="form-textarea mt-1 w-full block h-24 border-1 rounded border-blue-600"
          bind:value={response.details}
          name="details"
          rows="3"
          placeholder="Describe the issue."
          required
        />
      </div>
      <div class=" p-4 m-2 w-96 shadow-lg grid place-content-center">
        <button
          type="submit"
          class="inline-block px-4 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
        >
          Submit
        </button>
      </div>
    </div>
  </form>
</div>
